.profile-header {
    padding: 10px 20px 10px;
    display: flex;
    align-items: center;
    position: fixed;
    z-index: 50;
    left: 0;
    right: 0;
    background-color: rgb(255, 255, 255);
    max-height: 64px;
}

/* LOGO */

.logo {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 15%;
}

.logo-link {
    vertical-align: middle;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    text-decoration: none;
}

.logo-icon::before {
    content: url('../../assets/icons/google-icon.svg');
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 8px;
    margin-right: 5px;
}

.logo-text {
    font-size: 20px;
    font-weight: 500;
    color: #5f6368;
}

/* SEARCH INPUT */

.search-block {
    background: #f1f3f4;
    border-radius: 12px;
    margin-left: 30px;
}

.search-input {
    display: flex;
    align-items: center;
    justify-content: center;
}

.search-input > span {
    padding: 0 10px;
    position: relative;
}

.search-input > span > div {
    position: absolute;
    bottom: -17.75px;
    transition: .4s ease;
    border-radius: 20px;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.search-input > span > div:hover {
    background-color: #5f63683e;
}

.search-input > input {
    width: 600px;
    padding: 12px 20px;
    border: 1px solid transparent;
    background: #f1f3f4;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 1.5px;
    margin-left: 12px;
}

.search-input > input::placeholder {
    font-family: inherit;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 1.5px;
    color:rgb(169, 166, 166, 0.6);
}

.search-input > input:focus {
    outline: none;
}

/* icons */

.icons {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: flex-end;
}

.icons > span {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    cursor: pointer;
    margin-right: 15px;
    border-radius: 50%;
    transition: .5s ease;
}

.icons > span:hover {
    background: rgb(169, 166, 166, 0.3);
}

.icons > span > svg {
    object-fit: cover;
    width: 25px;
    height: 25px;
}

.icons-avatar {
    border-radius: 50%;
    width: 30px;
    height: 30px;
    cursor: pointer;
    box-sizing: content-box;
    border: 4px solid transparent;
    transition: .3s ease;
}

.icons-avatar:hover {
    border: 4px solid rgba(206, 205, 205, 0.425);
}

.icons-avatar > img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
}

/* PROFILE BODY */

.profile-body {
    display: flex;
    margin: 0 auto;
    padding: 64px 0 20px;
}

.profile-container {
    max-width: 1120px;
    margin: 0 auto;
}

/* NAVBAR */

.profile-navbar {
    width: 280px;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.profile-menu {
    list-style: none;
}

.profile-menu__link {
    text-decoration: none;
    color: rgb(60,64,67);
    margin-left: 15px;
    font-weight: 400;
}

.profile-menu__item {
    width: 100%;
    padding: 10px 0;
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
    padding-left: 10px;
    display: flex;
    align-items: center;
}

.profile-menu__item-active {
    background-color: rgb(232,240,254);
}

.profile-menu__link-active {
    color: rgb(25,103,210);
}

/* PROFILE - BODY - CONTENT */

.profile-content {
    max-width: 840px;
}

.profile-content__header {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    padding: 20px 0 50px;
}

.profile-content__header > h1,
.profile__review-text > h1,
.profile-settings__header > h2 {
    font-weight: 500;
}

.profile__review {
    display: flex;
    align-items: center;
    padding-bottom: 20px;
}

/* PROFILE - MAIN - FORM */

.profile-settings {
    border: 1px solid rgb(169, 166, 166);
    border-radius: 12px;
    margin-bottom: 30px;
}

.profile-settings__header {
    padding: 25px 20px 20px;
}

.profile-settings__header > p > span {
    color: rgb(26, 115, 232);
    cursor: pointer;
}

/* form */

.profile-form {
    pointer-events: all;
}

.profile-main__form {
    padding-bottom: 20px;
}

.profile-input:not(:last-child) {
    border-bottom: 1px solid rgb(169, 166, 166);
}

.profile-input {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 20px;
    transition: .5s ease;
    cursor: pointer;
}

.profile-input:hover {
    background: rgba(215, 213, 213, 0.3);
}

.profile-field__name {
    width: 20%;
}

.profile-field__name > small {
    font-weight: 700;
    color: #5f6368;
}

.profile-field__value {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 80%;
}

.profile-contact-btn {
    display: flex;
    background-color: transparent;
    border: 1px solid #5f6368;
    align-items: center;
    justify-content: center;
    border-radius: 19px;
    padding: 5px 10px;
}

.profile-contact-btn > big {
    font-size: 15px;
    margin-left: 10px;
}